












































import { Component, Vue } from "vue-property-decorator";
import Plan from "pev2";
import html2canvas from "html2canvas";
export const planData: any[] = ["", ""];
@Component({
  components: {
    Plan
  }
})
export default class App extends Vue {
  private planInput: string = "";
  private queryInput: string = "";
  private planSource: string = "";
  private planQuery: string = "";

  private submitPlan(): void {
    this.planQuery = this.queryInput;
    this.planSource = this.planInput;
    this.queryInput = "";
    this.planInput = "";
  }
  private backToForm(): void {
    this.planQuery = "";
    this.planSource = "";
  }
  private saveAsPNG(): void {
    const saveAs = (uri: string, filename: string) => {
      var link = document.createElement("a");

      if (typeof link.download === "string") {
        link.href = uri;
        link.download = filename;

        //Firefox requires the link to be in the body
        document.body.appendChild(link);

        //simulate click
        link.click();

        //remove the link when done
        document.body.removeChild(link);
      } else {
        window.open(uri);
      }
    };

    const planContainer = window.document.querySelector(
      "#plan .plan-container"
    )!;
    const plan: HTMLDivElement = planContainer.querySelector(
      ".overflow-auto"
    ) as HTMLDivElement;
    if (plan) {
      plan.classList.remove("overflow-auto");
      const subElement = plan.querySelector(".plan");
      planContainer.classList.remove("overflow-hidden");
      if (subElement) {
        subElement.classList.remove("w-100");
        subElement.classList.remove("h-100");
      }
      window.requestAnimationFrame(() => {
        html2canvas(plan as HTMLElement, {
          scrollX: 0,
          scrollY: -window.scrollY,
          width: plan.scrollWidth + 200,
          height: plan.scrollHeight + 100,
          windowWidth: plan.scrollWidth + 200,
          windowHeight: plan.scrollHeight + 100
        }).then(canvas => {
          saveAs(canvas.toDataURL(), "pgsql-plan.png");
          plan.classList.add("overflow-auto");
          planContainer.classList.add("overflow-hidden");
          if (subElement) {
            subElement.classList.add("w-100");
            subElement.classList.add("h-100");
          }
        });
      });
    }
  }
}
